'use client';

import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/lib/auth-context';
import JobCard from '@/components/JobCard';
import StatusBadge from '@/components/StatusBadge';
import { 
  Plus, Calendar, DollarSign, TrendingUp, 
  Clock, AlertCircle, ArrowRight
} from 'lucide-react';

export default function ClientDashboard() {
  const router = useRouter();
  const { user, getToken } = useAuth();
  const [bookings, setBookings] = useState([]);
  const [stats, setStats] = useState({
    total: 0,
    pending: 0,
    scheduled: 0,
    in_progress: 0,
    completed: 0,
    totalCost: 0,
  });
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Fetch bookings in dev mode even without user
    fetchBookings();
  }, [user]);

  const fetchBookings = async () => {
    try {
      const token = getToken();
      const headers = {};
      if (token) {
        headers['Authorization'] = `Bearer ${token}`;
      }
      
      const response = await fetch('/api/bookings', {
        headers,
      });

      if (response.ok) {
        const data = await response.json();
        setBookings(data.bookings || []);
        
        // Calculate stats
        const stats = {
          total: data.bookings?.length || 0,
          pending: data.bookings?.filter(b => b.status === 'pending').length || 0,
          scheduled: data.bookings?.filter(b => b.status === 'scheduled').length || 0,
          in_progress: data.bookings?.filter(b => b.status === 'in_progress').length || 0,
          completed: data.bookings?.filter(b => b.status === 'completed').length || 0,
          totalCost: data.bookings?.reduce((sum, b) => sum + (b.total_cost || 0), 0) || 0,
        };
        setStats(stats);
      }
    } catch (error) {
      console.error('Fetch bookings error:', error);
    } finally {
      setLoading(false);
    }
  };

  const recentBookings = bookings.slice(0, 5);
  const urgentBookings = bookings.filter(b => b.urgency === 'urgent' || b.urgency === 'emergency').slice(0, 3);

  return (
    <div className="space-y-8">
      {/* Header */}
      <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
        <div>
          <h1 className="text-4xl md:text-5xl font-black uppercase tracking-tighter italic mb-2 text-black">
            Dashboard
          </h1>
          <p className="text-gray-600">Welcome back, {user?.first_name || user?.email?.split('@')[0] || 'Guest User'}</p>
        </div>
        <motion.button
          whileHover={{ scale: 1.05 }}
          whileTap={{ scale: 0.95 }}
          onClick={() => router.push('/client/request')}
          className="px-6 py-4 bg-red-600 text-white font-black rounded-xl hover:bg-red-700 transition-all flex items-center gap-3 uppercase tracking-wider text-sm shadow-lg"
        >
          <Plus size={20} />
          Request Service
        </motion.button>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {[
          { label: 'Total Jobs', value: stats.total, icon: Calendar, color: 'blue' },
          { label: 'Pending', value: stats.pending, icon: Clock, color: 'yellow' },
          { label: 'In Progress', value: stats.in_progress, icon: TrendingUp, color: 'purple' },
          { label: 'Total Spent', value: `₦${stats.totalCost.toLocaleString()}`, icon: DollarSign, color: 'green' },
        ].map((stat, index) => (
          <motion.div
            key={index}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: index * 0.1 }}
            className="p-6 rounded-2xl bg-white border border-gray-200 hover:bg-gray-50 hover:border-red-300 transition-all shadow-sm"
          >
            <div className="flex items-center justify-between mb-4">
              <stat.icon size={24} className="text-red-600" />
              <StatusBadge status={stat.label === 'Pending' ? 'pending' : stat.label === 'In Progress' ? 'in_progress' : 'completed'} size="small" />
            </div>
            <p className="text-sm font-bold uppercase tracking-wider text-gray-600 mb-1">{stat.label}</p>
            <p className="text-2xl font-black text-black">{stat.value}</p>
          </motion.div>
        ))}
      </div>

      {/* Urgent Bookings */}
      {urgentBookings.length > 0 && (
        <div className="space-y-4">
          <div className="flex items-center gap-3">
            <AlertCircle size={24} className="text-red-600" />
            <h2 className="text-2xl font-black uppercase tracking-tight italic text-black">Urgent Requests</h2>
          </div>
          <div className="space-y-4">
            {urgentBookings.map((booking, index) => (
              <JobCard key={booking.id} job={booking} index={index} onClick={() => router.push(`/client/jobs/${booking.id}`)} />
            ))}
          </div>
        </div>
      )}

      {/* Recent Bookings */}
      <div className="space-y-4">
        <div className="flex items-center justify-between">
          <h2 className="text-2xl font-black uppercase tracking-tight italic text-black">Recent Jobs</h2>
          <button
            onClick={() => router.push('/client/jobs')}
            className="text-sm font-bold uppercase tracking-wider text-red-600 hover:text-red-700 transition-colors flex items-center gap-2"
          >
            View All
            <ArrowRight size={16} />
          </button>
        </div>
        {loading ? (
          <div className="text-center py-12 text-gray-400">Loading...</div>
        ) : recentBookings.length > 0 ? (
          <div className="space-y-4">
            {recentBookings.map((booking, index) => (
              <JobCard key={booking.id} job={booking} index={index} onClick={() => router.push(`/client/jobs/${booking.id}`)} />
            ))}
          </div>
        ) : (
          <div className="text-center py-12 space-y-4">
            <p className="text-gray-400">No jobs yet. Get started by requesting a service!</p>
            <motion.button
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              onClick={() => router.push('/client/request')}
              className="px-6 py-3 bg-gradient-to-r from-blue-600 to-green-600 text-white font-black rounded-xl hover:from-blue-500 hover:to-green-500 transition-all uppercase tracking-wider text-sm"
            >
              Request Your First Service
            </motion.button>
          </div>
        )}
      </div>
    </div>
  );
}
